import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Atividades_acao_imediata } from '../../../modelo/Atividades_acao_imediata'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'atividades_acao_imediata-dados-edit',   templateUrl: 'atividades_acao_imediata_dadosEdit.component.html' }) export class Atividades_acao_imediataDadosEditComponent implements OnInit {    atividades_acao_imediata: Atividades_acao_imediata;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.atividades_acao_imediata.Atividades_acao_imediata";                 let id = +params['id'];         this.atividades_acao_imediata = new Atividades_acao_imediata();         this.atividades_acao_imediata.cd_atividades_acao_imediata = id;         o.objetoJson = this.atividades_acao_imediata;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a atividades_acao_imediata. ';       }      this.atividades_acao_imediata = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


